import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BuildcardPage } from "../buildcard/buildcard";

import { Storage } from "@ionic/storage";
import { FormsModule } from '@angular/forms';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  username:string;
  password:string;
  warning:string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  verifyCredentials(){
    this.storage.get(this.username).then((data) =>{
       if(data === null){
          this.warning = "Username doesn't exist";
       } else {
         
          this.compareUsernamePassword(data.password);
       }
    });
  }

  compareUsernamePassword(password){

      var key = {
       username: this.username
       };

      if(this.password === password){
        
        this.navCtrl.push(BuildcardPage, key);
      } 
      else{
        this.warning = "Username and Password do not match";
      }
  }

  onLoadBuildCardPage(){

    /*var data = {
      name: this.fullname,
      email: this.email,
      phone: this.phone
    };

  	this.navCtrl.push(BuildcardPage, data);*/
  }
}



